html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #f5f5f5;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.right {
  margin: 0;
}
@media (min-width: 768px) {
  .right {
    display: flex;
    justify-content: right;
    width: 100%;
  }
}

.w-10 {
  width: 10rem;
}

.m-t-5 {
  margin-top: 5rem;
}

h1,
h2,
h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #191919;
  line-height: 1.8;
}

p,
a {
  font-family: "Roboto", sans-serif;
}

.header {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 12rem;
  }
}
.header .logo img {
  width: 13rem;
  height: 13rem;
}
.header .logout {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 3rem 3rem;
  cursor: pointer;
  color: #191919;
  text-align: center;
  transition: transform 0.3s;
  font-size: 2rem;
}
.header .logout:hover {
  color: #5f8870;
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .header .logout {
    margin: 0 0 0 4rem;
  }
}
.header .navegation {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column-reverse;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .navegation {
    flex-direction: row;
    margin-top: 0;
  }
}
.header .menu-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .menu-nav {
    display: flex;
    flex-direction: row;
    margin-top: 0;
  }
}
.header .menu-nav a {
  padding: 1rem;
  font-size: 1.5rem;
  color: #191919;
  transition: transform 0.3s;
}
.header .menu-nav a:hover {
  color: #5f8870;
  transform: scale(1.2);
}
@media (min-width: 480px) {
  .header .menu-nav a {
    font-size: 1.8rem;
  }
}
@media (min-width: 480px) {
  .header .menu-nav a {
    font-size: 2rem;
  }
}

.main {
  background-image: url(../img/background-header.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  max-height: 85rem;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .main {
    flex-direction: row;
    padding: 5rem;
    min-height: 70rem;
  }
  .main .information {
    padding: 0rem;
  }
}
.main .information {
  padding: 2rem;
  max-width: 100%;
}
.main .information .title {
  color: #f8f5f2;
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  width: 100%;
}
.main .information .message {
  width: 100%;
  color: #eff0f0;
  font-weight: 400;
  font-size: 1.8rem;
  text-align: center;
  padding: 0 1rem;
}
.main .information .message span {
  color: #f8f5f2;
  font-weight: 900;
}
@media (min-width: 1024px) {
  .main .information {
    padding: 5rem;
  }
  .main .information .title {
    font-size: 4rem;
  }
  .main .information .message {
    font-size: 2rem;
    text-align: left;
  }
}
.main .redirect {
  display: flex;
  justify-content: center;
  align-items: center;
}
.main .redirect .button-redirect {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.2rem 2rem;
  background-color: rgb(0, 120, 0);
  color: #f8f5f2;
  text-decoration: none;
  border-radius: 0.5rem;
  transition: background-color 0.3s;
  font-size: 1.8rem;
  font-weight: 700;
  transition: transform 0.3s;
}
.main .redirect .button-redirect:hover {
  background-color: #004500;
  transform: scale(1.2);
}
.main .redirect .button-redirect svg {
  margin-right: 1rem;
}
@media (min-width: 768px) {
  .main .redirect .button-redirect {
    padding: 1.5rem 3rem;
  }
}
@media (min-width: 1024px) {
  .main .information {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 50%;
  }
  .main .information .title {
    font-size: 5rem;
  }
  .main .information .message {
    font-size: 2.5rem;
    padding: 0 3.5rem;
  }
  .main .redirect {
    width: 50%;
  }
  .main .redirect .button-redirect svg {
    max-width: 4rem;
  }
}

.contenedor {
  text-align: center;
}
.contenedor h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #191919;
  margin: 6rem 0;
}
@media (min-width: 768px) {
  .contenedor h2 {
    font-size: 3rem;
  }
}

.botton {
  background-color: rgb(0, 120, 0);
  color: #f8f5f2;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin: 3rem 0 4rem 0;
  display: block;
  border: none;
  border-radius: 0.2rem;
  transition: transform 0.3s;
  padding: 1.5rem 3rem;
  margin: 4rem 0;
}
.botton:hover {
  background-color: #005f00;
  cursor: pointer;
}
.botton:hover {
  margin-right: 1.2rem;
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .botton {
    margin: 3rem 0;
  }
}

.container-adds {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .container-adds {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.add {
  position: relative;
  overflow: hidden;
  border-radius: 16px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}
.add:hover {
  transform: scale(1.05);
}
.add .add__img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  filter: brightness(90%);
  transition: filter 0.3s ease;
}
.add .add__img:hover {
  filter: brightness(100%);
}
.add .add__img .furniture {
  height: 30rem;
}
.add .add__img .add__img-src {
  height: 45rem;
}
.add .add-content {
  position: relative;
  background: linear-gradient(180deg, #ffffff, #f5f8f6);
  padding: 24px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
}
.add .add__name {
  font-size: 2.5rem;
  margin-bottom: 12px;
  color: #333;
  z-index: 1;
}
.add .add__description {
  color: #555;
  margin-bottom: 18px;
  font-size: 1.6rem;
  line-height: 1.8;
  text-align: center;
}
.add .add__button {
  align-self: center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: #fff;
  background: linear-gradient(#e44d26, #d13c1d);
  padding: 0.5rem 2rem;
  border-radius: 8px;
  transition: background 0.3s ease;
}
.add .add__button svg {
  width: 4rem;
  margin-right: 1rem;
}
.add .add__button p {
  margin: 0;
  color: #fff;
  font-size: 1.5rem;
}
.add .add__button:hover {
  background: linear-gradient(#d13c1d, #b83316);
}

.benefits-section h1 {
  color: #141f1a;
  text-align: center;
  font-size: 2.8rem;
  font-weight: 700;
  grid-column: 1/3;
  margin: 5rem 0;
  padding: 0 0.5rem;
}
@media (min-width: 768px) {
  .benefits-section {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .benefits-section h1 {
    font-size: 4rem;
  }
}
.benefits-section .benefit {
  padding: 2rem;
  text-align: center;
  background: linear-gradient(180deg, #ffffff, #f5f8f6);
  border-radius: 0.8rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
  transition: transform 0.3s ease;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.benefits-section .benefit .benefit__img img {
  width: 10rem;
  height: auto;
}
.benefits-section .benefit .benefit__title {
  font-size: 2rem;
  margin-top: 1rem;
  margin-bottom: 0.625rem;
}
.benefits-section .benefit .benefit__description {
  font-size: 1.8rem;
  color: #666;
}
.benefits-section .benefit:hover {
  transform: scale(1.05);
}

.contact {
  background-image: url(../img/contact.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  max-height: 70rem;
  text-align: center;
  color: #fff;
  padding: 4rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contact {
    height: 46rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contact .contact__message {
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact .contact__message h3 {
  font-weight: 700;
  font-size: 2.5rem;
}
.contact .contact__message p {
  font-size: 1.8rem;
  color: #e8f0eb;
}
@media (min-width: 768px) {
  .contact .contact__message h3 {
    font-size: 3rem;
  }
  .contact .contact__message p {
    font-size: 2rem;
  }
}
.contact .contact__icons-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact .contact__actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}
.contact .contact__actions a {
  display: block;
  transition: transform 0.3s;
}
.contact .contact__actions a:hover {
  transform: scale(1.2);
}
.contact .contact__actions .contact__icon img {
  width: 5rem;
  height: auto;
  margin: 0 1rem;
}
.contact .contact__button {
  margin: 5rem;
}
.contact .contact__button a {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact .contact__button a svg {
  margin-right: 1rem;
  max-width: 5rem;
}
.contact .button {
  padding: 1.5rem 3rem;
  background-color: rgb(0, 120, 0);
  color: #fff;
  text-decoration: none;
  border-radius: 0.8rem;
  transition: background-color 0.3s;
  font-size: 1.8rem;
  font-weight: 700;
}
.contact .button:hover {
  background-color: #004500;
}

footer {
  background-color: #191919;
  color: #fff;
  padding-top: 4rem;
  padding-bottom: 0;
  margin-top: 4rem;
}

.footer-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer-container {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.footer-container .right {
  gap: 5rem;
}

.footer-logo img {
  width: 8rem;
  height: auto;
}
.footer-logo p {
  color: #e8f0eb;
  font-size: 1rem;
}

.footer-links,
.footer-social,
.footer-attributions {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.footer-links h3,
.footer-social h3,
.footer-attributions h3 {
  color: #adc8b8;
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
}
.footer-links a,
.footer-social a,
.footer-attributions a {
  color: #f5f8f6;
  margin-bottom: 1rem;
  text-align: center;
}
.footer-links a:hover,
.footer-social a:hover,
.footer-attributions a:hover {
  color: #82a691;
}
@media (min-width: 768px) {
  .footer-links a,
.footer-social a,
.footer-attributions a {
    margin-bottom: 1rem;
    text-align: left;
  }
}

.footer-attributions p {
  color: #e8f0eb;
  margin-bottom: 0.5rem;
  margin-top: 0;
  text-align: center;
  font-size: 1rem;
}
.footer-attributions h3 {
  text-align: center;
}
.footer-attributions a {
  color: #adc8b8;
}
@media (min-width: 768px) {
  .footer-attributions p {
    text-align: left;
  }
  .footer-attributions h3 {
    text-align: left;
  }
}

.footer-derechos {
  height: 100%;
  background-color: #18251f;
  text-align: center;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 0 4rem;
}
.footer-derechos p {
  color: #f8f5f2;
  padding: 3rem 0;
  text-align: center;
  margin: 0;
  height: 100%;
}

.page-contact__title {
  margin: 8rem 0;
  font-size: 4rem;
  font-weight: 700;
  color: #2c3b33;
}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  max-height: 300rem;
  background: linear-gradient(to bottom, #2c3b33 50%, #f8f5f2 50%);
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .form-container {
    flex-direction: row;
    background: linear-gradient(to right, #2c3b33 50%, #f8f5f2 50%);
  }
}

.contact-socials {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 50%;
  width: 100%;
  margin: 0;
  padding: 0 2rem 2rem 2rem;
  position: relative;
  border-radius: 0 0 0 10px;
}
@media (min-width: 768px) {
  .contact-socials {
    width: 50%;
    height: 100%;
    padding: 0 10rem 10rem 10rem;
  }
}
.contact-socials .contact-socials__title {
  color: #f5f8f6;
  font-size: 3rem;
  font-weight: 700;
}
.contact-socials .contact-socials__message {
  color: #c2d6ca;
  font-size: 1.8rem;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 768px) {
  .contact-socials .contact-socials__message {
